<script>
import DotaService from '../DotaService';
import FlipCard from './FlipCard.vue';
import Carousel from './Carousel';
import Slide from './Slide';

export default {
  name: 'DotaComponent',
  components:{
    'FlipCard': FlipCard,
    Carousel,
    Slide,
  },
  data() {
    return {
      navNext: <img src='/assets/navigate_next_white.svg' />,
      navPrev: <img src='/assets/navigate_before_white.svg' />,
      dotalist: [],
      hype_loc: '',
      error: '',
      text: '',
    }
  },
  methods: {
    videoLinkGen: function(inputName){
      return "https://cdn.cloudflare.steamstatic.com/apps/dota2/videos/dota_react/heroes/renders/" + inputName.slice(14) + ".webm";
    },
    tagRemoval: function(input_str){
      let re = /<.*>/;
      var temp_str = input_str.replace(re,'');
      temp_str.replace(/<br>/,'');
      return temp_str;
    },
  },
  async created() {
    try{
      this.dotalist = await DotaService.getCharacters();
    } catch(err){
      this.error = err.message;
    }
  }
}
</script>

