/* eslint-disable */

import axios from 'axios';

const url = 'api/posts/';

class DotaService {
    //Get Post
    static getCharacters() {
        return new Promise(async (resolve, reject) => {
            try{
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(dota => ({
                        ...dota,
                    }))
                );
            } catch(err){
                reject(err);
            }
        });
    }
}

export default DotaService;